.page {
  font-family: Poppins, sans-serif;
  color: #fff;
  line-height: 140%;

  scroll-behavior: smooth;

  &:has(.page__menu:target) {
      overflow: hidden;
    }

  &__body {
    margin: 0;
    background-color: $menu-text-color;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all $effect-duration;
    opacity: 0;
    transform: translateX(100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
